body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #333;
}

header {
    background-color: #2C3E50;
    color: white;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

header h1 {
    margin: 0;
    font-size: 2.5em;
    letter-spacing: 2px;
}

nav {
    margin-top: 10px;
}

nav button {
    padding: 10px 20px;
    background-color: #E74C3C;
    color: white;
    border: none;
    cursor: pointer;
    margin: 0 10px;
    border-radius: 30px;
    transition: background-color 0.3s, box-shadow 0.3s;
    font-size: 1.1em;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

nav button:hover {
    background-color: #C0392B;
}

main {
    padding: 20px;
}

.section-title {
    font-size: 2em;
    margin: 20px 0 10px;
    color: white;
    text-align: center;
    font-weight: bold;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
}

article {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    display: flex;
    flex-direction: column;
}

article img {
    max-width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
}

article h3 {
    margin: 10px 0;
    font-size: 1.5em;
    color: #333;
}

article p {
    color: #555;
    margin: 10px 0;
}

article a {
    text-decoration: none;
    color: #E74C3C;
    font-weight: bold;
    margin-top: auto;
    transition: color 0.3s;
}

article a:hover {
    text-decoration: underline;
    color: #C0392B;
}

footer {
    background-color: #2C3E50;
    text-align: center;
    padding: 20px;
    color: #faf6f6;
    margin-top: 40px;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

footer img {
    width: 150px;
    margin-bottom: 10px;
}

footer p {
    font-size: 0.9em;
    color: #ccc;
    margin: 0;
}

footer a {
    text-decoration: none;
    color: #E74C3C;
    transition: color 0.3s;
}

footer a:hover {
    color: #C0392B;
}
