body {
  background-color: #333;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #2c3e50;
  padding: 20px;
  box-shadow: 0 4px 10px #0003;
}

header h1 {
  letter-spacing: 2px;
  margin: 0;
  font-size: 2.5em;
}

nav {
  margin-top: 10px;
}

nav button {
  color: #fff;
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 30px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 1.1em;
  transition: background-color .3s, box-shadow .3s;
  box-shadow: 0 3px 6px #0000001a;
}

nav button:hover {
  background-color: #c0392b;
}

main {
  padding: 20px;
}

.section-title {
  color: #fff;
  text-align: center;
  margin: 20px 0 10px;
  font-size: 2em;
  font-weight: bold;
}

.grid-container {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  display: grid;
}

article {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

article img {
  border-radius: 5px;
  max-width: 100%;
  margin-bottom: 10px;
}

article h3 {
  color: #333;
  margin: 10px 0;
  font-size: 1.5em;
}

article p {
  color: #555;
  margin: 10px 0;
}

article a {
  color: #e74c3c;
  margin-top: auto;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

article a:hover {
  color: #c0392b;
  text-decoration: underline;
}

footer {
  text-align: center;
  color: #faf6f6;
  background-color: #2c3e50;
  margin-top: 40px;
  padding: 20px;
  box-shadow: 0 -2px 5px #0003;
}

footer img {
  width: 150px;
  margin-bottom: 10px;
}

footer p {
  color: #ccc;
  margin: 0;
  font-size: .9em;
}

footer a {
  color: #e74c3c;
  text-decoration: none;
  transition: color .3s;
}

footer a:hover {
  color: #c0392b;
}
